.dropdown {
  position: relative;
  display: inline-block;
}
.dropdown>button>img {
 height: 80px;
 width: 70px;
 transition: transform .5s ease-in-out;

}
.dropdown>button {
  background: none;
  position: absolute;
  margin-top: -45px;
  margin-left: -70px;
 
 }
.dropdown>button>img:hover {
  
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
  
 
 }
.dropdown-content {
  display: none;
  position: absolute;
  margin-left: -230px;
  margin-top: 20px;
  background-color: #f9f9f9;
  min-width: 160px;
  overflow: auto;
  box-shadow: -1px 1px 9px 1px rgba(161, 145, 158, 0.7);
  border-radius: 10px;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: #f1f1f1;
}

.show {
  display: block;
}
